.bannerRegion{
    display: flex;
    width: 93%;
    margin: 0 auto 0 auto;
    background-image: url('./../../assets/coverpage.png');
    background-position:  bottom right;
    background-repeat: no-repeat;
    background-size: cover;
    height: 700px;
    padding: 0;
}

.clickRegion{
    padding: 15% 3% 10% 4%;
    text-align: center;
    background-color: rgba(0,0,0,0.8);
    width: 100%;
}
.enda{
    font-weight: 700;
    font-size: 2.5rem;
    text-transform: uppercase;
    line-height: 1.3;
    color: white;
    text-align: center;
}
.btnBanner{
    border: none;
    padding: 4% 15% 4% 15%;
    cursor: pointer;
    margin-top: 2.5%;
    font-weight: 700;
    color: whitesmoke;
    border-radius: 30px;
    font-size: 1.2rem;
    background-color: #cf0606;
    margin: 2.5% auto 0px auto;
}
.btnBanner:hover{
    background-color: whitesmoke;
    color: #cf0606;
    font-size: 1.2rem;
}
@media screen and (max-width: 500px) {
    .bannerRegion{
        width: 88.7%;
    }
}
@media screen and (max-width: 700px) {
    .bannerRegion{
        width: 89%;
    }
}
@media screen and (max-width: 900px) {
    .bannerRegion{
        width: 90.5%;
    }
    
}
@media screen and (max-width: 1200px) {
    .bannerRegion{
        width: 91%;
    }
    
}