.kontaktcouple{
    width: 93%;
    margin: 0px auto 0px auto;
    background-image: url('../../assets/kontaktpic1.png');
    background-repeat: no-repeat;
    background-size: cover;
    padding: 8%;
    background-position: bottom;
}
.kontaktheader{
    font-weight: 700;
    font-size: 3.5rem;
    color: #f31d1d;
}
input{
    display: block;
}
textarea{
    display: block;
    width: 90%;
    height: 10rem;
    border: none;
}
.kontaktinput{
    width: 90%;
    padding: 2% 20%;
    border: none;
}
.submit{
    padding: 1.5% 15%;
    margin-top: 2%;
    border: none;
    cursor: pointer;
    font-weight: 700;
}